.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;


    &--header {
        display: flex;
        justify-content: space-between;
        padding-top:20px;
        @media screen and (min-width: 768px) {
            padding-top:40px;
        }
    }

  @media screen and (min-width: 480px) {
    width: 480px;
  }


  @media screen and (min-width: 768px) {
    width: 768px;
    padding-right: 32px;
    padding-left: 32px;
  }

  @media screen and (min-width: 1680px) {
    width: 1680px;
    padding-right: 16px;
    padding-left: 16px;
  }
}
